import ResponseButton from 'components/ResponseButton';
import SurveyStep from 'components/SurveyStep';
import { useModuleBodyStylesEffect, useModuleStyles } from 'hooks/module-styles';
import { StepConfig, SurveyConfig } from 'types/configs';
import { Dispatch } from 'react';
import { Action } from 'types/reducer';
import { Progress, State } from 'types/state';
import styles from './style.module.css';

interface Props {
  config: SurveyConfig;
  state: State;
  dispatch: Dispatch<Action>;
}

const getProgress = (configArray: StepConfig[], state: State): Progress => {
  const progress: Progress = {
    current: 0,
    total: 0,
  };
  for (let i = 0; i < configArray.length; i++) {
    const stepConfig = configArray[i];
    if (stepConfig.type === 'game') continue;

    const { restartProgress, skipProgress } = stepConfig;
    const { current, total } = progress;

    if (i > state.step && restartProgress) break;

    progress.current =
      i > state.step ? current : restartProgress ? 1 : skipProgress ? current : current + 1;
    progress.total = restartProgress ? 1 : skipProgress ? total : total + 1;
  }

  return progress;
};

const Survey = ({ config, state, dispatch }: Props) => {
  const moduleClasses = useModuleStyles(styles);
  useModuleBodyStylesEffect(styles);

  const configArray = Object.values(config);
  const stepConfig = configArray[state.step];

  if (stepConfig.type === 'game') return <></>;

  const progress = getProgress(configArray, state);

  return (
    <div className={moduleClasses('survey-container')}>
      {stepConfig ? (
        <>
          <h2 className={moduleClasses('survey-title')}>{stepConfig.title ?? 'Questionnaire'}</h2>
          <SurveyStep
            config={stepConfig}
            state={state}
            progress={progress}
            dispatch={dispatch}
            moduleClasses={moduleClasses}
          />
        </>
      ) : (
        <>
          <div>
            <h3>Responses:</h3>
            <ul>
              <li>
                <b>friends-at-finish:</b> {state.profile.friends}
              </li>
              <li>
                <b>followers-at-finish:</b> {state.profile.followers}
              </li>
              {Object.values(state.responses).map(({ id, response }) => (
                <li key={id}>
                  <b>{id}</b>: {response.join('; ')}
                </li>
              ))}
            </ul>
            {/* <pre>{JSON.stringify(responses, undefined, 2)}</pre> */}
          </div>

          <div className={moduleClasses('survey-button-next')}>
            <ResponseButton
              className={moduleClasses('survey-button-next')}
              onClick={() => dispatch({ type: 'RESET' })}
            >
              Restart
            </ResponseButton>
          </div>
        </>
      )}
    </div>
  );
};

export default Survey;
