const SightSharePage = () => {
  const [state, dispatch] = usePersistedReducer(
    'surveyState',
    reducerWithConfig(config),
    initialState,
    initWithConfig(config),
    isCurrentState,
  );

  // useEffect(() => {
  //   const hh = async () => {
  //     const h = await hash(JSON.stringify(config));
  //     console.log(h);
  //   };
  //   hh();
  // });

  // const [{ step: stepState, responses }, setState] = useState(initialState);

  // const { step: stepParam } = useParams();

  // const maxStep = Object.keys(responses).length;
  // const urlStep = parseInt(stepParam || '-1', 10);
  // const step = !isNaN(urlStep) && urlStep >= 0 && urlStep < maxStep ? urlStep : maxStep;

  // console.log('state', state);
  const configArray = Object.values(config);
  const stepConfig = configArray[state.step];

  // const navigate = useNavigate();
  if (state.step === 0) console.log({ config, configArray });
  console.log({ step: state.step, id: stepConfig?.id });

  const devButtons = (
    <>
      <button onClick={() => dispatch({ type: 'DEC_FRIENDS' })}>Decrease Friends</button>
      <button onClick={() => dispatch({ type: 'RES_FRIENDS' })}>Reset Friends</button>
      <button
        style={{
          width: '100px',
          marginTop: '30px',
        }}
        onClick={() => dispatch({ type: 'RESET' })}
      >
        Restart Game
      </button>
    </>
  );

  if (stepConfig && stepConfig.type === 'game') {
    return (
      <>
        <Game config={stepConfig} state={state} dispatch={dispatch} />
        {process.env.NODE_ENV !== 'production' && devButtons}
      </>
    );
  } else {
    return (
      <>
        <Survey config={config} state={state} dispatch={dispatch} />
        {process.env.NODE_ENV !== 'production' && (
          <button
            style={{
              width: '100px',
              marginTop: '30px',
            }}
            onClick={() => dispatch({ type: 'RESET' })}
          >
            Restart
          </button>
        )}
      </>
    );
  }
};

export default SightSharePage;
